:root {
    --aside: #262D31;
    --mesage-received: #262D31;
    --message-sent: #056162;
    --online: #1DD200;
    --head-chat: #2A2F32;
    --chat: #333A41;
    --input-chat: #2A2F32;

    --bs-primary: #0d6efd;
    --bs-primary-opacity: rgb(13, 110, 253, 0.7);
}

#message-input {
    height: 65%;
    border: 1px solid transparent;
    outline: none;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    padding-left: 10px;
    background: rgb(27, 27, 27);
    color: white;
    transition: 0.3s ease;
}

#message-input:focus {
    border: 1px solid var(--bs-primary);
}

#message-submit {
    border: none;
    cursor: pointer;
    height: 65%;
    padding: 0 15px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: var(--bs-primary);
    transition: 0.3s ease;
}

#message-submit:hover {
    background-color: #0f3d81;
}

.chat {
    display: grid;
    grid-template-rows: 60px calc(90% - 60px) 10%;
}

#chat-input {
    grid-row: 3/4;
    height: 100%;
    width: 100%; 
    padding: 0 150px; 
    background: var(--input-chat);
}

.texto {
    background-image: linear-gradient(-20deg, #b721ff 0%, #21d4fd 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    color: tomato;
    font-size: 50px;
    font-weight: bold;
    font-family: 'Ubuntu', sans-serif;
}

.dropdown-menu {
    background-color: #6C757D !important;
}

#dropdown-item-button {
    background-color: transparent !important;
    border: none !important;
}

.user-sidebar {
    height: 72px;
    background: #262D31;
    border: none;
    border-bottom: 0.1em solid rgb(68, 68, 68);
    transition: 0.3s ease;
}

.user-sidebar:hover {
    background: #323739;
}

.user-sidebar-active {
    background: #323739;
}

#img-chat {
    width: 45px;
    border-radius: 50%;
}

.button-group {
    border: none;
    background: #5159ff;
    height: 50px;
    transition: .3s ease;
}

.button-group-active {
    background: #4147CE;
}

.form{
    width: 300px;
}

.container-full{
    height: 100vh;
}

.aside-color {
    background-color: var(--aside);
}

.mr-10 {
    margin-right: 10px;
}

.header-height {
    height: 60px
}

.w-50 {
    width: 50%;
}

.column-chat-dimensions {
    height: 100vh;
    width: 100%;
}

.message-received {
    padding-left: 10px;
    padding-right: 20px;
    background: var(--mesage-received);
    margin-left: 60px;
    max-width: 60%;
    /* font-size: .8em; */
}

.message-sent {
    margin-right: 60px;
    background-color: var(--message-sent);
    max-width: 60%;
    /* font-size: .8em; */
}

.text-online {
    color: var(--online);
}

.online-height {
    height: 45vh;
}

.all-height {
    height: 55vh;
}

.bg-head-chat {
    background-color: var(--head-chat);
}

.bg-chat {
    background-color: var(--chat);
}

.mr-1rem {
    margin-right: 1rem;
}

.button-info {
    width: 40px;
    height: 40px;
    border: none;
    background-color: transparent;
    color: #fff;
    outline: none;
    border-radius: 50%;
    font-size: 22px;
    transition: 0.4s ease;
}

.svg {
    display: block;
    width: 50%;
    margin: 0 auto;
}

/* GRID dashboardscreen */

#container-grid {
    display: grid;
    grid-template-columns: 350px 1fr;
}

.sidebar-close {
    border: none;
    background: transparent;
}

.aside-responsive {
    display: none;
}

.mx-alert {
    margin: 0 20%;
}

.text-date {
    font-size: 11px;
}

@media screen and (max-width: 900px){
    .aside-responsive {
        display: block;
        z-index: 99;
        position: absolute;
        left: -50%;
        transition: 0.3s ease;
        height: 100vh;
        width: 50%;
    }
    .aside-responsive-show {
        left: 0;
    }
    #container-grid {
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 800px){
    #chat-input {
        padding: 0 10%;
    }
}

@media screen and (max-width: 600px){
    .aside-responsive {
        display: block;
        z-index: 99;
        position: absolute;
        left: -100%;
        transition: 0.3s ease;
        height: 100vh;
        width: 100%;
    }
    .aside-responsive-show {
        left: 0;
    }
    #container-grid {
        grid-template-columns: 1fr;
    }
    .message-received{
        margin-left: 5%;
    }
    .message-sent{
        margin-right: 5%;
    }
    .chat-container{
        width: 100%;
    }
    .chat {
        grid-template-rows: 65px calc(92% - 65px) 8%;
    }
    .header-height {
        height: 65px;
    }
    .all-height {
        height: 50vh;
    }
    .mx-alert {
        margin: 0 10%;
    }
}

@media screen and (max-width: 500px) {
    .text-resp {
        font-size: .9rem;
    }
    .text-msg-res {
        font-size: .9rem;
    }
    #img-chat {
        width: 35px;
    }
    #message-input {
        font-size: .9rem;
    }    
    .text-date {
        font-size: 10px;
    }
}

@media screen and (max-width: 400px) {
    .mr-1rem {
        margin-right: 5px;
    }
}

@media screen and (max-width: 330px) {
    .text-resp {
        font-size: .8rem;
    }
    #message-input {
        font-size: .8rem;
    }
    .mx-alert {
        margin: 0 5%;
    }
    .text-date {
        font-size: 9px;
    }
}
@media screen and (max-width: 370px) {
    .text-msg-res {
        font-size: .8rem;
    }
    #message-input {
        height: 70%;
    }
    #message-submit {
        height: 70%;
    }
    #chat-input {
        padding: 0 5%; 
    }
    
}
@media screen and (max-width: 335px) {
    .text-msg-res {
        font-size: .7rem;
    }
    .mr-1rem {
        margin-right: 2px;
    }
}