@import url('./spinner.css');
@import url('./global.css');

* {
  font-family: 'Poppins', sans-serif;
}
html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
    width: 10px;
}
::-webkit-scrollbar-track{
    background-color: transparent;
}
::-webkit-scrollbar-thumb {
    background-color: rgb(88, 88, 88);
}
